
          @import "@/assets/themes/AppAddict/variables";
          @import "@/assets/core";
        

@import "@/assets/variables";

a {
  word-break: break-all;
}

.main {
  display: flex;

  @include notebook {
    flex-direction: column;
  }
}

.news {
  flex-grow: 1;

  &__item {
    background-image: var(--news-article-gradient);
    border-radius: 10px;
  }

  &__heading {
    color: var(--text-primary);
    font-weight: 600;
    font-size: 20px;
    padding: 10px;
    border-bottom: 1px solid #e5e5e5;
  }

  &__date {
    display: block;
    color: $text-secondary;
    font-size: 12px;
    padding: 10px 10px 0;
  }

  &__text {
    padding: 10px;
    line-height: 17px;

    &::v-deep p {
      margin: 5px 0;
    }

    &::v-deep em {
      font-style: italic;
    }

    &::v-deep {

      blockquote {
        font-style: italic;
        margin-left: 15px;
        text-decoration: underline;
        text-decoration-style: dotted;
      }
      ol {
        list-style: decimal inside;
      }

      ul {
        list-style: disc inside;
      }
    }
  }

  &__separator {
    content: "";
    display: block;
    height: 15px;
  }
}

.pagination {
  margin-top: 15px;

  &__icon {
    &_rotate {
      transform: rotate(180deg);
    }
  }

  &__link {
    color: var(--text-tertiary);
    font-weight: 600;

    &_disabled {
      pointer-events: none;
      opacity: 0.5;
    }

    &:first-child {
      margin-right: 25px;

      .pagination__icon {
        margin-right: 5px;
      }
    }

    &:last-child {
      .pagination__icon {
        margin-left: 5px;
      }
    }
  }
}

.sidebar {
  width: 400px;
  flex: 0 0 400px;
  margin-left: 20px;

  @include notebook {
    width: 100%;
    flex: 0 0 100%;
    margin-left: 0;
    margin-top: 15px;
  }
}

.list {
  background-image: var(--news-sidebar-gradient);
  border-radius: 10px;

  &__heading {
    display: flex;
    align-items: center;
    color: var(--text-primary);
    font-size: 20px;
    font-weight: 600;
    padding: 15px;

    @include notebook {
      padding: 10px;
    }
  }

  &__links {
    max-height: 315px;
    overflow: scroll;

    @include notebook {
      max-height: 240px;
    }
  }

  &__icon {
    margin-right: 15px;

    @include notebook {
      margin-right: 10px;
    }
  }

  &__link {
    display: block;
    padding: 7px 15px;
    font-weight: 400;
    word-break: break-word;

    @include notebook {
      padding: 7px 10px;
    }

    &.router-link-exact-active,
    &_active {
      background: var(--news-active-color);
      color: var(--text-tertiary);
    }
  }
}
